@use "./config.scss" as config;


.container-padded {
  padding: config.$app-height*0.07 config.$content-width*0.07;
  overflow: visible;
  display: flex;
  margin: none;
  width: config.$content-width;
}


.scroll-container {

  overflow-y: scroll;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */

  &::-webkit-scrollbar {
    display: none;
  }
}